import React, { Component } from 'react';

export default class Diet extends Component {
  render() {
    return (
      <div className="Diet">
        <h3>Diet</h3>
        <h1>
          Narwhals eat griffins. Exclusively.
        </h1>
      </div>
    );
  }
}


